// components/LaptopList.tsx
import React, { useEffect, useState } from 'react';
import axios from 'axios';

interface Laptop {
  _id: string;
  name: string;
  brand: string;
  model: string;
  price: number;
  processor: string;
  description?: string;
  category?: string;
  image: string;
  usageType: string;
}

const LaptopList: React.FC = () => {
  const [laptops, setLaptops] = useState<Laptop[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');

  useEffect(() => {
    axios.get('http://localhost:5000/api/laptops')
      .then(response => {
        setLaptops(response.data);
        setLoading(false);
      })
      .catch(error => {
        console.error('Error fetching laptops:', error);
        setError('Error fetching laptops');
        setLoading(false);
      });
  }, []);

  if (loading) {
    return <p>Loading...</p>;
  }

  if (error) {
    return <p>{error}</p>;
  }

  return (
    <div>
      <h1>List of Laptops</h1>
      <ul>
        {laptops.map(laptop => (
          <li key={laptop._id}>
            <h2>{laptop.name}</h2>
            <p><strong>Brand:</strong> {laptop.brand}</p>
            <p><strong>Model:</strong> {laptop.model}</p>
            <p><strong>Price:</strong> ${laptop.price}</p>
            <p><strong>Processor:</strong> {laptop.processor}</p>
            <p><strong>Usage Type:</strong> {laptop.usageType}</p>
            <img src={laptop.image} alt={laptop.name} width="200" />
            <p>{laptop.description}</p>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default LaptopList;
